body{
    font-family: Nunito;
}

main{
    max-width:800px;
    margin: 0 auto;
}

.info{
    background-color: rgb(201, 201, 201);
    padding: 0.5em 2em;
    border-radius:0.5em;
    p{
        margin:0.3em;
    }

}


.user-photo{
  
    object-fit: cover;
    width:100%;
    height:100%;
}
    
.picture{
    height:200px;
    width:150px;
    background-color: rgb(182, 193, 253);
    border-radius: 1em;
    border: 1px solid rgb(182, 193, 253);
    overflow:hidden;
    margin:0.3em;
        
  }

  .background{
    background-image: url('https://images.unsplash.com/photo-1528362426376-962bcf4f76c7?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=859&q=80');
    background-size: cover;
    background-position: bottom left;
    background-color: rgb(198, 199, 202);
    height: 220px;
    border-radius: 0.7em;
    display: flex;
    align-items:flex-end;

  }

a {
    margin: 1em;
    display:inline-block;
    margin-left: 0;
    padding: 0.2em 1.5em;
    background-color: rgb(66, 128, 223);
    border-radius:1em;
    color: rgb(255, 255, 255);
    text-decoration:none;
    font-weight: 700;
}
